import React from 'react';
import { Route, withRouter } from 'react-router-dom';
import HowToPlay from './HowToPlay';
import LoginForm from './LoginForm';
import Play from './Play';
import Shared from './Shared';
import SignUpForm from './SignUpForm';
import SplashPage from './SplashPage';



function LoggedOutPageContent() {
    return (
        <>
            <Route path="/" exact component={SplashPage} />
            <Route path="/quick-play" exact component={Play} />
            <Route path="/shared" exact component={Shared} />
            <Route path="/how-to-play" exact component={HowToPlay} />
            <Route path="/log-in" exact component={LoginForm} />
            <Route path="/join" exact component={SignUpForm} />
        </>
    );
}

export default withRouter(LoggedOutPageContent);